@font-face {
    font-family: mulish;
    src: url(../public/Mulish-VariableFont_wght.ttf);
}

*{ 
    margin:0 ; 
    padding:0;
    box-sizing:border-box;
}

.App {
    overflow: hidden;
}

.flex-row{
    display: flex;
    flex-direction: row;
}

.flex-col {
    display: flex;
    flex-direction: column;
}

.circle-pattern .row .circle{
    background-color: rgba(151, 195, 249, 0.535);
}