.services{
    align-items: center;
    /* justify-content: center; */
    position: relative;
    margin-top: 145px;
}
.services > *{
    margin: 25px 0;

}
.services .first{
    align-items: center;    
}

.services .first > *{
    margin: 15px;
}
.services .first .title{
    font-size: 36px;
}

.services .first .description{
    font-size: 18px;
    width: 952px;
    text-align: center;
    color: rgba(0, 0, 0, 0.428);
}

.services .first hr{
    color:black;
    width: 56px;
    height: 2px;
    background-color: black;
    border: none;
}

.services .cardList{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    max-width: 1120px;
    width: 100%;
}

.services .cardList .card{
    height: 354px;
    width:350px;
    padding:50px 40px ;
    border-radius: 23px;
    margin: 20px 0;
    background-color: #fff;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.services .cardList .card h1{
    font-size: 24px;
    margin: 25px 0 20px;
}

.services .cardList .card p{
    font-size:16px;
    color: rgba(0, 0, 0, 0.428);
    line-height: 28px;
}

.services button{
        border: solid 2px #458FF6;
        color: #458FF6;
        background-color: #fff;
        width: 200px;
        height: 55px;
        border-radius: 30px;
        font-size: 18px;
        font-weight: 550;
}

.services .vector{
    position:absolute;
    z-index: -1;
    right: 600px;
    top: 218px;
}

.services .circle-pattern {
    z-index: -1;
    position: absolute;
    top: 535.5px;
    right: -10px;

}