.header {
    justify-content: space-between;
    align-items: center;
    display: relative;
    margin:90px 0 0 0;
}
.header .first {
    width: 445px;
    height: 338px;
    justify-content: space-between;
    margin-left: 0;
}

.header .first .title{
    font-size: 48px;
}

.header .first .description{
    font-size: 21px;
    color: #6E6E6E;
}

.header .first button{
    background-color:#458FF6;
    color: white;
    border: none;
    /* padding: 10px 20px; */
    width: 200px;
    height: 55px;
    border-radius: 30px;
    font-size: 18px;    
    font-weight: 300;
}

.header .circle-pattern{
    position: absolute;
    right: 1600px;
    bottom: -5px;
}