.navbar{
    color:#1F1534;
    font-family: mulish ;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    margin-top: 60px;
}

.navbar .first {
    width: 155px;
    height: 42px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}
.navbar .first .logo {
    background-color: #458FF6;
    height: 40px;
    width: 40px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    color:white;
    font-size: 26px;
    font-weight: 600;
}

.navbar .first .title{
    font-size: 24px;
    font-weight: 600;
}


.navbar nav{
    font-size: 18px;
    color: #1f15347d;
    width: 545px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.navbar nav:first-child{
    color: #1F1534;
}