.articles {
    height: 800px ;
    box-sizing: border-box;
    /* margin-bottom: 300px; */
    align-items: center;
    justify-content: space-between;
    position: relative;

}
.articles .first {
    align-items: center;
    margin: 0 auto;
    /* color: #fff; */

}
.articles .first h1 {
    margin-bottom: 25px;
    font-size: 36px;
    line-height: 56px;
}

.articles .first hr {
    color: black;
    width: 56px;
    height: 2px;
    background-color: black;
    border: none;
}

.articles .second {
    align-items: center;
    justify-content: space-between;
    /* margin: 25px auto; */
    /* color: white; */
    border-top-right-radius: 10px;

}

.articles .second .card2 {
    height: 512px;
    width:350px;
    margin: 20px;
    background-color: #fff;

}

.articles .second .card2 {
    border-radius: 19px;
}
.articles .second .card2  img{
    width: 350px;
    height: 240px;
    /* margin: 0 20px; */
}

.articles .second .card2 .texts {
    width: 266px;
    margin: 30px;
    justify-self: space-between;
}
.articles .second .card2 .title {
    font-size: 21px;
    line-height: 32px;
    font-weight: 750;
    margin-bottom: 25px;
}

.articles .second .card2 .description {
    font-size: 16px;
    line-height: 28px;
    color: #6E6E6E;
}
.articles .second .card2 text{
    color: #458FF6;
    background-color: #fff;
    /* width: 200px;
    height: 55px; */
    border-radius: 30px;
    font-size: 18px;
    font-weight: 600;
    margin-top: 25px;
}
.articles button {
    border: solid 2px #458FF6;
        color: #458FF6;
        background-color: #fff;
        width: 200px;
        height: 55px;
        border-radius: 30px;
        font-size: 17px;
        line-height: 28px;
        font-weight: 550;
}

.articles .vector {
    position: absolute;
    height: 550px;
    z-index: -1;
    transform: rotate(180deg);
    top: 100px;
    left: 872px;
}

.articles .circle-pattern {
    position: absolute;
    top: 106px;
    left: -12px;
    z-index: -1;
}