.lhp {
    justify-content: space-between;
    position: relative;
    align-items: center;
    margin: 200px 0 130px 0;
    /* width: 100%; */

}

.lhp .second {
    width: 450px;
    height: 400px;
    justify-content: space-between;
    margin-left: 50px;
}

.lhp .second .title {
    font-size: 36px;
    line-height: 55px;
}

.lhp .second hr {
    color: black;
    width: 56px;
    height: 2px;
    background-color: black;
    border: none;
}
.lhp .second .description {
    font-size: 18px;
    line-height: 30px;
    color: #6E6E6E;
}

.lhp .second button {
    border: solid 2px #458FF6;
        color: #458FF6;
        background-color: #fff;
        width: 200px;
        height: 55px;
        border-radius: 30px;
        font-size: 18px;
        font-weight: 550;
}

.lhp .circle-pattern {
    position: absolute;
    right: -145px;
    bottom: -77px;
}