.testimonials {
    justify-content: space-between;
    width: 1120px;
    height: 425px;
    background-image: linear-gradient(200deg, #43afd3 0%, #3a49f2ee 100%);
    border-radius: 20px;
    padding: 40px 115px 70px 115px;
    margin: 0px auto 260px auto;
    position: relative;
}

.testimonials .first {
    align-items: center;
    margin: 25px auto ;
    color:#fff;
}

.testimonials .first .title {
    font-size: 36px;
    line-height: 48px;
    font-weight: 550;
    margin-bottom: 25px;
}

.testimonials .first hr {
    color: black;
        width: 56px;
        height: 2px;
        background-color: #fff;
        border: none;
}

.testimonials .second  {
    align-items: center;
    justify-content: space-between;
    height: 133px;
    /* color: white; */
}

.testimonials .second img {
    width: 133px;
    height: 133px;
    background-color: white;
    border-radius: 50%;
    margin-right: 30px;
    padding: 3.5px;

}
.testimonials .second .second-f  {
    align-items: center;
    color: white;    
}

.testimonials .second .second-f .name {
    font-size: 22px;
    line-height: 48px;
}

.testimonials .second .second-f .title {
    font-size: 18px;
    font-weight: 300;
}

.testimonials .second .second-s {
    width: 420px;
    height: 120px;
    color: white;
    line-height: 30px;
    font-size: 19px;
    font-weight: 250;
}

.testimonials #first  {
    position: absolute;
    right:10px;
    top:10px;
}
.testimonials #second {
    position: absolute;
    left: -65px;
    bottom: 20px;
}