@font-face {
    font-family: mulish;
    src: url(../../public/Mulish-VariableFont_wght.ttf);
}

footer{
    justify-content: space-between;
    height: 432px;
    background-color: #509ed6;
    color:white;
    padding: 100px 210px 100px 120px;
    font-family: mulish;
    position: relative;
}

footer .first{
    width: 391px;
    height: 214px;
    justify-content: space-between;
}

footer .first .logo {
    align-items: center;
    justify-content: space-between;
    width: 160px;
}
footer .first .logo div{
    background-color: white;

    height: 40px;
    width: 40px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    color:#458FF6;
    font-size: 26px;
    font-weight: 600;
}
footer .first .logo h1 {
    font-size: 24px;
}
footer .first .description{
    font-size: 18px;
    line-height: 28px;
    font-weight: 200;
}

footer .first .copyright {
    font-weight: 200;
    font-size: 16px;
}


footer .second {
    justify-content: space-between;
    width: 660px;
    height: 214px;
}
footer .second > * {
    justify-content: space-between;
}

footer .second .links h1{
    font-size: 20px;
    line-height: 55px;
    font-weight: 500;
}

footer .second .links p{
    font-size: 18px;
    line-height: 38px;   
}

.footer #first{
    top: -78px;
    right: 86px;
    position:absolute;
}

.footer #second{
    bottom: 2px;
    left: 1px;
    position:absolute;
}