.download {
    justify-content: space-between;
    align-items: center;
    margin: 130px auto 200px auto;
    width: 100%;

}

.download .first {
    width: 486px;
    height: 388px;
    margin:100px;
    justify-content: space-between;
}

.download .first .title {
    font-size: 36px;
    line-height: 55px;
}

.download .first hr {
    color: black;
    width: 56px;
    height: 2px;
    background-color: black;
    border: none;
}

.download .first .description {
    font-size: 18px;
    line-height: 30px;
    color: #6E6E6E;
}

.download .first button {
    border: solid 2px #458FF6;
    color: #458FF6;
    background-color: #fff;
    width: 200px;
    height: 55px;
    border-radius: 30px;
    font-size: 18px;
    font-weight: 550;
}